.App {
    font-family: sans-serif;
    text-align: center;
  }
  h1,
  p {
    font-family: Montserrat, Arial, Helvetica, sans-serif;
    text-align: center;
  }
  
  button {
    background-color: #5a6672;
    border-radius: 15px;
    color: white;
    padding: 15px 32px;
    margin: 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    font-weight: bold;
  }
  
  .flex-container {
    display: flex;
    justify-content: center;
  }
  
  table {
    border: 2px solid #354a5f;
    font-family: Montserrat, Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  th {
    border-bottom: 2px solid #354a5f;
    padding: 8px;
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #354a5f;
    color: white;
    text-align: center;
  }
  tr:nth-child(even) {
    background-color: #ddd;
  }
  tr:hover {
    background-color: #cacacb;
  }
  
  td {
    text-align: center;
    padding: 8px;
  }
  
  h1 {
    padding: 50px;
    text-align: center;
    background: #030e1a;
    color: rgb(216, 169, 14);
    font-size: 60px;
  }
  